:root {
  --duration-timing: 250ms cubic-bezier(.4, 0, .2, 1);
  --indent: 20px;
  --items: 4;
}

* {
  box-sizing: border-box;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: currentColor;
  text-decoration: none;
}

.search-form {
  width: 100%;
  background: #005bbb;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  box-shadow: 0 1px 2px 1px rgba(6, 6, 63, .3);
}

.input {
  width: 20%;
  font: inherit;
  background-color: #fff;
  border: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 8px 10px;
}

.search-btn {
  width: 2.5em;
  cursor: pointer;
  transition: background-color var(--duration-timing), fill var(--duration-timing), box-shadow var(--duration-timing);
  background-color: #f8b007;
  border: none;
  border-radius: 0 4px 4px 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-btn:hover, .search-btn:focus {
  fill: #071ff8;
  background-color: #ff0;
  box-shadow: 3px 3px 3px rgba(81, 81, 199, .12), 0 4px 4px #5151c7, 1px 4px 6px rgba(81, 81, 199, .16);
}

.load-more {
  font: inherit;
  color: #fff;
  cursor: pointer;
  transition: background-color var(--duration-timing), color var(--duration-timing);
  background-color: #071ff8;
  border: none;
  border-radius: 5px;
  margin: 25px auto 0;
  padding: 8px 20px;
  display: block;
}

.load-more:hover, .load-more:focus {
  color: #071ff8;
  background-color: #f8b007;
}

.is-hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
  padding-top: 50px;
  display: flex;
}

.info {
  gap: 5px;
  display: flex;
}

.info-item {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px 30px;
  display: flex;
}

.gallery__link {
  transition: box-shadow var(--duration-timing);
  width: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
  transition: box-shadow var(--duration-timing);
  display: block;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.gallery__link:hover, .gallery__link:focus {
  box-shadow: 3px 3px 3px rgba(81, 81, 199, .12), 0 4px 4px #5151c7, 1px 4px 6px rgba(81, 81, 199, .16);
}

.photo-card__img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  margin-bottom: 10px;
}

